//
// C3 Charts
// --------------------------------------------------

.c3 svg {
  padding-left: 4px;
  width: 100%;
  font: 10px sans-serif;
  -webkit-tap-highlight-color: transparent;
}

.c3 path, .c3 line {
  fill: none;
  stroke: #9f9f9f;
}

.c3 text {
  fill: #9b9b9b;
  font-size: 12px;
  font-weight: 400;
  font-family: $font-family-base;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.c3-legend-item-tile,
.c3-xgrid-focus,
.c3-ygrid,
.c3-event-rect,
.c3-bars path {
  shape-rendering: crispEdges;
}

.c3-chart-arc path {
  stroke: #fff;
}

.c3-chart-arc text {
  fill: #fff;
  font-size: 13px;
}

/*-- Axis --*/
/*-- Grid --*/
.c3-grid line {
  stroke: #eaebee;
}

.c3-grid text {
  fill: #aaa;
}

.c3-xgrid,
.c3-ygrid {
  stroke-dasharray: 0 0;
}

/*-- Text on Chart --*/
.c3-text.c3-empty {
  fill: #808080;
  font-size: 2em;
}

/*-- Line --*/
.c3-line {
  stroke-width: 3px;
}

/*-- Point --*/
.c3-circle._expanded_ {
  stroke-width: 2px;
  stroke: white;
}

.c3-selected-circle {
  fill: white;
  stroke-width: 2px;
}

/*-- Bar --*/
.c3-bar {
  stroke-width: 0;
}

.c3-bar._expanded_ {
  fill-opacity: 0.75;
}

/*-- Focus --*/
.c3-target.c3-focused {
  opacity: 1;
}

.c3-target.c3-focused path.c3-line, .c3-target.c3-focused path.c3-step {
  stroke-width: 2px;
}

.c3-target.c3-defocused {
  opacity: 0.3 !important;
}

/*-- Region --*/
.c3-region {
  fill: steelblue;
  fill-opacity: .1;
}

/*-- Brush --*/
.c3-brush .extent {
  fill-opacity: .1;
  fill: #000;
}

/*-- Select - Drag --*/
/*-- Legend --*/
.c3-legend-item {
  font-size: 12px;
}

.c3-legend-item-hidden {
  opacity: 0.15;
}

.c3-legend-background {
  opacity: 0.75;
  fill: white;
  stroke: lightgray;
  stroke-width: 1;
}

/*-- Title --*/
.c3-title {
  font: 14px sans-serif;
}

/*-- Tooltip --*/
.c3-tooltip-container {
  z-index: 10;
}

.c3-tooltip {
  border-collapse: collapse;
  border-spacing: 0;
  background-color: #fff;
  empty-cells: show;
  -webkit-box-shadow: 7px 7px 12px -9px #777;
  -moz-box-shadow: 7px 7px 12px -9px #777;
  box-shadow: 7px 7px 12px -9px #777;
  opacity: 0.9;
}

.c3-tooltip tr {
  border: 1px solid #ccc;
}

.c3-tooltip th {
  background-color: #aaa;
  font-size: 14px;
  padding: 2px 5px;
  text-align: left;
  color: #fff;
}

.c3-tooltip td {
  font-size: 13px;
  padding: 3px 6px;
  background-color: #fff;
  color: $black;
  border-left: 1px dotted #999;
}

.c3-tooltip td > span {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 6px;
}

.c3-tooltip td.value {
  text-align: right;
}

/*-- Area --*/
.c3-area {
  stroke-width: 0;
  opacity: 0.2;
}

/*-- Arc --*/
.c3-chart-arcs-title {
  dominant-baseline: middle;
  font-size: 1.3em;
}

.c3-chart-arcs .c3-chart-arcs-background {
  fill: #e0e0e0;
  stroke: none;
} 

.c3-chart-arcs .c3-chart-arcs-gauge-unit {
  fill: #000;
  font-size: 16px;
}

.c3-chart-arcs .c3-chart-arcs-gauge-max {
  fill: #777;
}

.c3-chart-arcs .c3-chart-arcs-gauge-min {
  fill: #777;
}

.c3-chart-arc .c3-gauge-value {
  fill: #000;
}

.c3 .domain {
  stroke: #c8c8c8;
  //stroke-dasharray: 3px 2px; 
}

.d3-chart {
  .tick line {
    display: none;
  }

  .c3-tooltip-container {
    border-radius: $border-sm;
  }

  .c3-tooltip {
    opacity: 1;
    color: $white;
    background: $primary;

    tr th {
      padding: 8px 10px;
      color: $white;
      background: $secondary;
    }

    tr td {
      padding: 5px 10px;
      color: $body-color;
      background: $white;
    }

    .value {
      border-left: 1px solid $gray-100;
    }
  }
}

#line-chart {
  // Tooltip
  .c3-tooltip-container {
    border-radius: 9px;
    overflow: hidden;
    //display: block !important; 
    tr { border: 0; }
   .name {
     border-left: 0;
   }
    table {
      border: 0 solid transparent;
      border-radius: 9px;
    }
  }

  // Axes
  .c3-axis-y-label {
    transform: translateY(-10px);
  }

  .c3-axis-y {
    .tick text {
      transform: translateX(1px);
    }
  }

  .c3-axis-x {
    text-align: left;
    text {
      transform: translateX(-1px);
      text-anchor: start !important;
    }
  }

  // Legend
  .c3-legend-item {
    display: none !important;
    transform: translateY(15px);
  }

  .c3-legend-item text {
    font-size: 16px;
    color: $gray-700;
  }
}

.d3-chart-wrap {
  width: 100%;
  max-width: 100%;
  overflow-y: hidden;
  overflow-x: auto;
  padding-bottom: 30px;

  > .d3-chart {
    min-width: 520px;
  }
}

.d3-chart-legend {
  @include group(30px, 5px);
  text-align: center;

  span {
    position: relative;
    display: inline-block;
    font-size: 16px;
    color: $gray-700;
    cursor: pointer;

    &::before {
      content: '';
      display: inline-block;
      height: 3px;
      width: 30px;
      margin-right: 6px;
      vertical-align: middle;
    }

    &:nth-child(1) {
      &::before {
        background: #a820d3;
      }
    }
    &:nth-child(2) { 
      &::before {
        background: #ed1c94;
      }
    }
  }

  @include media-breakpoint-up(xxl) {
    span {
      font-size: 18px;
    }
  }
}

* + .d3-chart-legend {
  margin-top: 20px;
}

@include media-breakpoint-up(xxl) {
  * + .d3-chart-legend {
    margin-top: 33px;
  }
}
