/*
*
* Snackbars
* --------------------------------------------------
*/


.snackbars {
  padding: 9px 16px;
  margin-left: auto;
  margin-right: auto;
  color: $white;
  text-align: left;
  background-color: $gray-700;
  border-radius: 0;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.15);

  font-size: 14px;

  .icon-xxs {
    font-size: 18px;
  }

  // padding on text
  p span:last-child {
    padding-left: 14px;
  }

  // snackbar icon on the left side
  &-left {
    display: inline-block;
    margin-bottom: 0;
  }

  // snackbar link or button on the right side
  &-right {
    display: inline-block;
    float: right;
    text-transform: uppercase;

    &:hover {
      text-decoration: underline;
    }
  }

  @include media-breakpoint-up(sm) {
    max-width: 540px;
    padding: 12px 15px;
    font-size: 15px;
  }
}


.inline-message {
  border-radius: $border-sm;
  padding: 20px;
  text-align: left;
  font-size: 15px;
  color: $gray-700;
  background: rgba($gray-100, 1);
  border: 2px dashed rgba($gray-500, .1);
}

* + .inline-message {
  margin-top: 20px;
}
