    background: linear-gradient(to right, #2e1a47 20%, #120a22 40%, #f6cc1b 60%, #2e1a47 80%);
    background-size: 200% auto;
    background-clip: text;
    text-fill-color: transparent;
    -webkit-background-clip: text;
